import React from "react";
import {Button, Card, Divider, Typography, DatePicker} from "antd";
import moment, {Moment} from "moment";
import {displayErrorMessage, formatListingPrice} from "../../../../lib/utils";
import {Viewer} from "../../../../lib/types";
import { Listing as ListingData } from "../../../../lib/graphql/queries/Listing/__generated__/Listing";
import {BookingsIndex} from "./types";

const {Paragraph, Title, Text} = Typography;

interface Props {
  // 
  bookingsIndex:ListingData["listing"]["bookingsIndex"],
  host:ListingData["listing"]["host"],
  viewer: Viewer; 
  price: number;
  // 
  checkInDate: Moment | null;
  checkOutDate: Moment | null;
  setCheckInDate: (checkInDate: Moment | null) => void;
  setCheckOutDate: (checkOutDate: Moment | null) => void;
  // 
  setModalVisible: (modalVisible: boolean) => void;
}


export const ListingCreateBooking =
  ({
     bookingsIndex,
     host,
     viewer,
     price,
     checkInDate,
     checkOutDate,
     setCheckInDate,
     setCheckOutDate,
     setModalVisible
   }: Props) => {
    //  JSON
    const bookingsIndexJSON: BookingsIndex = JSON.parse(bookingsIndex);
    /**
     * - 
     *    * false
     * 
     * - 
     *    * 
     * 
     * - 
     *    * 
     * 
     * @param currentDate 
     */
    const disabledDate = (currentDate?: Moment | null) => {
      if (currentDate) {
        const dateIsBeforeEndOfDay = currentDate.isBefore(moment().endOf("day"));
        const dateIsMoreThenThreeMouthsAhead = moment(currentDate).isAfter(
          moment()
          .endOf('day')
          .add(90,"days")
        )
        return (
          dateIsBeforeEndOfDay ||
          dateIsMoreThenThreeMouthsAhead ||
          dateIsBooked(currentDate)
        )
      } else {
        return false;
      }
    };
    /**
     * - 
     * - currentDate =>   
     * - bookingsIndex === true)
     * @param currentDate 
     */
    const dateIsBooked = (currentDate: Moment) => {
      const year = moment(currentDate).year();
      const month = moment(currentDate).month();
      const day = moment(currentDate).date();
      if (bookingsIndexJSON[year] && bookingsIndexJSON[year][month]) {
        return Boolean(bookingsIndexJSON[year][month][day]);
      } else {
        return false;
      }
    };
    /**
     * - 
     * 
     * - 
     * 
     * @param selectedCheckOutDate 
     */
    const verifyAndSetCheckOutDate = (selectedCheckOutDate: Moment | null) => {
      if (checkInDate && selectedCheckOutDate) {
        if (moment(selectedCheckOutDate).isBefore(checkInDate, "days")) {
          return displayErrorMessage(
            ``
          );
        }
        /**
         * 
         * 
         *  - 
         * 
         * dateCursor gorw
         *      return
         *       
         */
        let dateCursor = checkInDate;
        while (moment(dateCursor).isBefore(selectedCheckOutDate, "days")) {
          // grow
          dateCursor = moment(dateCursor).add(1, "days");
          // match
          const year = moment(dateCursor).year();
          const month = moment(dateCursor).month();
          const day = moment(dateCursor).date();
          if (
            bookingsIndexJSON[year] &&
            bookingsIndexJSON[year][month] &&
            bookingsIndexJSON[year][month][day]
          ) {
            return displayErrorMessage(
              ""
            );
          }
        }
      }
      setCheckOutDate(selectedCheckOutDate);
    };
    const checkOutInputDisabled = !checkInDate;
    const checkInInputDisabled = !viewer.id;
    const buttonDisabled = !checkInDate || !checkOutDate;
    const viewerIsHost = viewer.id === host.id;
    let buttonMessage = "";
    if (!viewer.id) {
      buttonMessage = "";
    } else if(viewerIsHost) {
      buttonMessage = "";
    }else if (!host.hasWallet) {
      buttonMessage =
        "Stripe ...   ";
    }  
    return (
      <div className="listing-booking">
        <Card className="listing-booking__card">
          <div>
            <Paragraph>
              <Title level={2} className="listing-booking__card-title">
                {formatListingPrice(price)}
                <span>/</span>
              </Title>
            </Paragraph>
            <Paragraph>
              
            </Paragraph>
            <Divider/>
            <div className="listing-booking__card-date-picker">
              <Paragraph strong></Paragraph>
              <DatePicker
                value={checkInDate ? checkInDate : undefined}
                onChange={dataValue => setCheckInDate(dataValue)}
                format={"YYYY/MM/DD"}
                disabledDate={disabledDate}
                disabled={checkInInputDisabled}
                showToday={false}
                onOpenChange={() => setCheckOutDate(null)}
              />
            </div>
            <div className="listing-booking__card-date-picker">
              <Paragraph strong></Paragraph>
              <DatePicker
                value={checkOutDate ? checkOutDate : undefined}
                onChange={dataValue => verifyAndSetCheckOutDate(dataValue)}
                format={"YYYY/MM/DD"}
                disabledDate={disabledDate}
                showToday={false}
                disabled={checkOutInputDisabled}
              />
            </div>
          </div>
          <Divider/>
          <Button
            disabled={buttonDisabled}
            size="large"
            type="primary"
            className="listing-booking__card-cta"
            onClick={() => setModalVisible(true)}
          >
            
          </Button>
          <Text type="secondary" mark>
            {buttonMessage}
          </Text>
        </Card>
      </div>
    );
  }
